import { useEffect, useState } from 'react';


function MyComponent() {
    const [error, setError] = useState(null);
    const [forwardPE, setItems] = useState(0);

    // Note: the empty deps array [] means
    // this useEffect will run once
    // similar to componentDidMount()
    useEffect(() => {
        fetch("https://yahoo-finance-low-latency.p.rapidapi.com/v11/finance/quoteSummary/UWMC?modules=defaultKeyStatistics%2CassetProfile", {
            "method": "GET",
            "headers": {
                "x-rapidapi-key": "c855900aafmshb5d7c3e12bbc769p180117jsn87216bd9ab88",
                "x-rapidapi-host": "yahoo-finance-low-latency.p.rapidapi.com"
            }
        })
            .then(res => res.json())
            .then(
                (result) => {

                    setItems(result.quoteSummary.result[0].defaultKeyStatistics.forwardPE.fmt);
                    console.log(result);
 
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }, [])

    if (error) {
        return <div>Error: {error.message}</div>;
    } else {
        return (
            <div>
                <p className="SotDEle"> Ticker: UWMC </p>
                <br></br>
                <p className="SotDEle"> Forward PE Ratio: {forwardPE}</p>
            </div>
        );
    }
}



export default MyComponent;

