
import React, { useReducer } from 'react';
import { useEffect, useState } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import { Bar, Line, defaults } from 'react-chartjs-2';
import Chart from 'chart.js';





function Sentiment() {

    const [stockInfo, updatestock] = useState('');

    return (
        <div>

            <Link to="/"> Return to Home</Link>


            <div>
                <br />
                <br />
                <StockSearch />
            </div>
            <SentimentView />
            {/* <InsertError /> */}
        </div>



    )


    function StockSearch() {
        const [formStock, updateForm] = useState('');

        return (
            <div>
                <span> Search for stock: </span><span><input className='formInput' onChange={evt => updateForm(evt.target.value)} type='text'></input></span> <button onClick={buttonClick} id="addButton">Confirm</button>
           
            </div>
            
        )

        async function buttonClick() {


            /*  await fetch(`https://socialsentiment-io.p.rapidapi.com/stocks/${formStock}/sentiment/daily/`, {
                "method": "GET",
                "headers": {
                    "x-rapidapi-key": "c855900aafmshb5d7c3e12bbc769p180117jsn87216bd9ab88",
                    "x-rapidapi-host": "socialsentiment-io.p.rapidapi.com"
                }
            })
                .then((response) => { 
                    return response.json()}
                ).then((result) => {
    
                    updatestock(result[7]);
                    console.log(stockInfo);
    
                })
                .catch(err => {
                    console.error(err);
                });
            */
    
            let obj = {
                name: formStock,
                score: 5,
                postive_score: 5,
                negative_score: -2,
                activity: 300,
    
            }
    
    
            updatestock(obj);
            return;
        }
    
    
    }

    function SentimentView() {
    
        const state = {
            labels: ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'],
            datasets: [
                {
                    label: 'Rainfall',
                    backgroundColor: 'rgba(53,122,14,1)',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: [65, 59, 80, 81, 56]
                }
            ]
        }

        defaults.borderColor = 'white';

        return (
            <div>
            <div>
                <Bar
                    data={state}
                    options={{
                        responsive: true,
                        maintainAspectRatio: false,
                        layout: {
                            padding: {
                                top: 5,
                                left: 15,
                                right: 25,
                                bottom: 15
                            }
                        },
                        title: {
                            display: true,
                            text: 'Average Rainfall per month',
                            fontSize: 20
                        },
                        legend: {
                            display: true,
                            position: 'right'
                        }
                    }}
                    width={10}
                    height={300}

                />
            </div>

            <div>
                <p>Test</p>
            </div>
            </div>
        )
    }


}

export default Sentiment
